	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="Account" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="Account" clearable  :readonly="ro.zhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="Account" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="Account" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="Password" prop="mima" >
					<el-input v-model="ruleForm.mima" placeholder="Password" clearable  :readonly="ro.mima"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="Password" prop="mima" >
					<el-input v-model="ruleForm.mima" placeholder="Password" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="Name" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="Name" clearable  :readonly="ro.xingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="Name" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="Name" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="Gender" prop="xingbie" >
					<el-select :disabled="ro.xingbie" v-model="ruleForm.xingbie" placeholder="Please select Gender"  filterable>
						<el-option
							v-for="(item,index) in xingbieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="Gender" prop="xingbie" >
					<el-input v-model="ruleForm.xingbie"
						placeholder="Gender" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.touxiang" label="Profile picture" prop="touxiang" >
					<file-upload
						tip="Click to upload Profile picture"
						action="file/upload"
						:limit="3"
						:disabled="ro.touxiang"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="touxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.touxiang" label="Profile picture" prop="touxiang" >
					<img v-if="ruleForm.touxiang.substring(0,4)=='http'&&ruleForm.touxiang.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang" width="100" height="100">
					<img v-else-if="ruleForm.touxiang.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.touxiang.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="Type" prop="leixing" >
					<el-select :disabled="ro.leixing" v-model="ruleForm.leixing" placeholder="Please select Type"  filterable>
						<el-option
							v-for="(item,index) in leixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="Type" prop="leixing" >
					<el-input v-model="ruleForm.leixing"
						placeholder="Type" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="Contact details" prop="lianxifangshi" >
					<el-input v-model="ruleForm.lianxifangshi" placeholder="Contact details" clearable  :readonly="ro.lianxifangshi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="Contact details" prop="lianxifangshi" >
					<el-input v-model="ruleForm.lianxifangshi" placeholder="Contact details" readonly></el-input>
