<template>
	<div class="container" :style='{"margin":"0 200px 20px"}'>
		<el-alert title="" type="success" :closable="false"></el-alert>
		<!-- <div class="top-content">
      <span></span>
      <el-input style="width:300px" v-model="name" placeholder=""></el-input>
      <span style="margin-left:20px">Account</span>
      <el-input style="width:300px" v-model="account" placeholder="Account"></el-input>
    </div> -->
		<!-- <div class="price-content">
      <span></span>
      <span>99.0</span>
    </div> -->
		<div class="pay-type-content">
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/weixin.png" alt>
				<!-- <span></span> -->
			</div>
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/zhifubao.png" alt>
				<!-- <span></span> -->
			</div>
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/jianshe.png" alt>
				<!-- <span></span> -->
			</div>
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/nongye.png" alt>
				<!-- <span></span> -->
			</div>
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/zhongguo.png" alt>
				<!-- <span></span> -->
			</div>
			<div class="pay-type-item">
				<el-radio v-model="type" label=""></el-radio>
				<img src="@/assets/img/test/jiaotong.png" alt>
				<!-- <span></span> -->
			</div>
		</div>
		<div class="buton-content">
			<el-button @click="submitTap" type="primary"></el-button>
			<el-button @click="back()">Back</el-button>
		</div>
	</div>
</template>
<script>
	// import { Message } from "element-ui";
	export default {
		data() {
			return {
				name: "",
				account: "",
				type: "",
				table: "",
				obj: ""
			};
		},
		mounted() {
			let table = this.$storage.get("paytable");
			let obj = this.$storage.getObj("payObject");
			this.table = table;
			this.obj = obj;
		},
		methods: {
			submitTap() {
				if (!this.type) {
					this.$message.error("Please select ");
					return;
				}
				this.$confirm(`Confirmed?`, "tip", {
					confirmButtonText: "Confirmed",
					cancelButtonText: "Cancel",
					type: "warning"
				}).then(async () => {
					this.obj.ispay = "";
					let changeType = true
					let errMsg = ''
					if(!changeType) {
						this.$message.error(errMsg);
						return false
					}
					if(!changeType) {
						this.$message.error(errMsg);
						return false
					}
					this.$http({
						url: `${this.table}/update`,
						method: "post",
						data: this.obj
					}).then(async ({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.$router.go(-1);
								}
							});
						} else {
							this.$message.error(data.msg);
						}
					});
				});
			},
			back() {
				this.$router.go(-1);
			}
		}
	};
</script>
<style lang="scss" scoped>
	.container {
		margin: 10px;
		font-size: 14px;

		span {
			width: 60px;
		}

		.top-content {
			display: flex;
			align-items: center;
			padding: 20px;
		}

		.price-content {
			display: flex;
			align-items: center;
			margin-top: 20px;
			padding-bottom: 20px;
			padding: 20px;
			border-bottom: 1px solid #eeeeee;
			font-size: 20px;
			font-weight: bold;
			color: red;
		}

		.pay-type-content {
			display: flex;
			align-items: center;
			margin-top: 20px;
			flex-wrap: wrap;

			span {
				width: 100px;
			}

			.pay-type-item {
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 300px;
				margin: 20px;
				border: 1px solid #eeeeee;
				padding: 20px;
				background: #fff;
			}
		}

		.buton-content {
			margin: 20px;
		}
	}
</style>